          <p class="text">
            9300
            ISO1000101002006000
            1000100
            
          </p>
        </div>
        <ul class="cpshow">
          <li class="cpshowli" v-for="(item, index) in cpshowli" :key="index">
            <div
              class="mainpic"
              :style="{backgroundImage: 'url(' +httpUrl+ item.image_url+ ')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
            ></div>
          </li>
        </ul>
      </div>
      <!--  -->
      <div class="ideabox pub" v-show="curindex == 1">
        <div
          class="mainpic"
          :style="{backgroundImage: 'url(' + require('../assets/about/2-1.png')+ ')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
        ></div>
        <div class="culture">
          <h1> </h1>
          <p class="english">OUR NANHONG OUR CORPORATE CULTURE</p>
          <p class="text"></p>
          <p class="text"></p>
          <p class="text"></p>
          <p class="text"></p>
        </div>
      </div>
      <!--  -->
      <div class="staffshow pub" v-show="curindex == 2">
        <ul class="stafflist">
          <li class="staffli" v-for="(item, index) in ygfc" :key="index">
            <div
              class="mainpic"
              :style="{backgroundImage: 'url(' +httpUrl+ item.image_url+  ')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
            ></div>
            <div class="infos">
              <p class="text"></p>
              <p class="des">{{item.title}}</p>
            </div>
          </li>
        </ul>
      </div>
      <!--  -->
      <div class="Honor pub" v-show="curindex == 3">
        <div class="top">
          <p class="title"></p>
          <p class="des"></p>
        </div>
        <ul class="honorlist">
          <li class="honorli" v-for="(item, index) in honor" :key="index">
            <div class="honorbox" v-if="index === 1 ||index === 3">
              <div class="honorinfo">
                <div class="text">
                  <p class="year">
                    {{item.year}}
                    <img src="../assets/about/honorr.png" alt />
                  </p>
                  <p class="intro">{{item.title}}</p>
                </div>
              </div>
              <div
                class="mainpic"
                :style="{backgroundImage: 'url('  +httpUrl+ item.image_url+ ')',
                  backgroundSize:'cover',
                  backgroundRepeat: 'no-repeat',
                  backgroundPosition:'center'
                    }"
              ></div>
            </div>
            <div class="honorbox" v-else>
              <div
                class="mainpic"
                :style="{backgroundImage: 'url('  +httpUrl+ item.image_url+')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
              ></div>
              <div class="honorinfo">
                <div class="text">
                  <p class="year">
                    <img src="../assets/about/honorl.png" alt />
                    {{item.year}}
                  </p>
                  <p class="intro">{{item.title}}</p>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <div class="pics">
          <img src="../assets/about/01.png" alt />
          <img src="../assets/about/02.png" alt />
          <img src="../assets/about/03.png" alt />
        </div>
      </div>
      <!--  -->
      <div class="sale pub" v-show="curindex == 4">
        <div
          class="topmainpic"
          :style="{backgroundImage: 'url(' + require('../assets/about/5-1.png')+ ')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
        ></div>
      </div>
      <!--  -->
      <div class="framework pub" v-show="curindex == 5">
        <div
          class="mainpic"
          :style="{backgroundImage: 'url(' + require('../assets/about/6-1.png')+ ')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
        ></div>
      </div>
      <!--  -->
      <div class="socia pub" v-show="curindex == 6 || curindex == 7">
        <ul class="szlist recruitment">
          <li class="newsli" v-for="(item, index) in lb" :key="index">
            <div
              class="mainpic"
              :style="{backgroundImage: 'url(' +require('../assets/about/7-1.png') + ')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
            ></div>
            <div class="ins-info">
              <p class="title">{{item.title}}</p>
              <p class="time">{{item.created_time}}</p>
              <p class="intro">{{item.desc}}</p>
              <p class="more" @click="torecruit(index,item)"></p>
            </div>
          </li>
        </ul>
        <!--  -->
        <mo-pagination
          :page-index="currentPage"
          :total="count"
          :page-size="pageSize"
          @change="pageChange"
        ></mo-pagination>
      </div>
      <!--  -->

      <!--  -->
      <div class="contact pub" v-show="curindex == 8">
        <div class="contactbox" v-html="lxwm"></div>
        <!-- <div class="contactbox">
          <div
            class="warrper"
            :style="{backgroundImage: 'url(' + require('../assets/about/8-1.png')+ ')',
             backgroundSize:'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition:'center'
            }"
          >
            <h1></h1>
            <p class="totel">
              
              <span>0830-6027777</span>
            </p>
          </div>
          <div class="telbox text">
            <p></p>
            <p>
              
              <span>0830-6027777</span>,
              <span>18181878760</span>,
              <span>18090855133</span>
            </p>
            <p>
              
              <span>0830-3380333</span>,
              <span>13320783392</span>
            </p>
          </div>
          <div class="address text">
            <p>71301</p>
            <p>
              
              <span>0830-6027777</span>
            </p>
          </div>
          <div class="branch text">
            <p>513</p>
            <p>
              
              <span>0830-6027777</span>
            </p>
            <p>
              
              <span>0830-6027777</span>
            </p>
            <p>
              Q Q
              <span>2073376722</span>
            </p>
            <p>
              
              <span>20008345@qq.com</span>
            </p>
          </div>
          <div class="map"></div>
        </div>-->
      </div>
    </div>
  </div>
</template>
<script>
import banner from "../components/homebanner";
import tabBar from "../components/tabBar";
import moPagination from "../components/pagenation";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "about",
  data() {
    return {
      // 
      pageSize: 24, // 20
      currentPage: 1, // 
      count: 300, // ,
      curindex: 0,
      shopshowinfo: [],
      tabtitle: {
        a: "",
        b: ""
      },
      tabnav: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      cpshowli: [],
      ygfc: [],
      branchlist: [
        {
          img: require("../assets/about/3-1.png"),
          name: "",
          address: "",
          tel: "000-000000"
        },
        {
          img: require("../assets/about/3-1.png"),
          name: "",
          address: "",
          tel: "000-000000"
        },
        {
          img: require("../assets/about/3-1.png"),
          name: "",
          address: "",
          tel: "000-000000"
        }
      ],
      lb: [],
      honor: [],
       lxwm: [],
      zptype: 0
      // swiperOptionThumbs: {
      //   // loop:true,
      //   spaceBetween: 10,
      //   touchRatio: 0.2,
      //   slideToClickedSlide: true,
      //   slidesPerView: 4,
      //   watchSlidesVisibility: true /*bug*/,
      //   navigation: {
      //     nextEl: ".swiper-button-next",
      //     prevEl: ".swiper-button-prev"
      //   }
      // }
    };
  },
  created() {
    this.requst();
    let idx = sessionStorage.getItem("mnavindex");
    if (!idx) {
      this.curindex = 0;
    } else {
      if (idx == 1) {
        this.curindex = 6;
      }
      if (idx == 2) {
        this.curindex = 4;
      }
      if (idx == 4) {
        this.curindex = 1;
      }
      if (idx == 5) {
        this.curindex = 7;
      }
      if (idx == 8) {
        this.curindex = 3;
      }
    }
  },
  methods: {
    requst(id) {
      this.$axios.post("/index/api/about", { id: id }).then(res => {
        this.cpshowli = res.data.data.child;
        this.ygfc = res.data.data.team;
        this.shopshowinfo = this.ygfc[0];
        this.honor = res.data.data.honor;
        this.lxwm = res.data.data.lxwm.content;
        // console.log(this.shopshowinfo);
        // console.log(res.data, "rrr");
      });
    },
    // 
    getList(page) {
        this.requstzp(this.zptype, page, 3);
    },
    pageChange(index) {
      this.currentPage = index;
      // console.log( this.currentPage)
      this.getList(index);
    },
    tonav(index) {
      //  console.log(index)
      this.curindex = index;
       if (index == 6) {
        this.zptype = 0;
        this.requstzp(0, 1, 3);
      }
      if (index == 7) {
        this.zptype = 1;
        this.requstzp(1, 1, 3);
      }
    },
        requstzp(type, page, limit) {
      this.$axios
        .post("/index/api/jobList", { type: type, page: page, limit: limit })
        .then(res => {
          this.lb = res.data.data.data;
          this.count = res.data.data.total;
          // console.log(res.data.data, "a");
        });
    },
    swiper(index, item) {
      this.shopshowinfo = item;
    },
    toprev() {},
    tonext() {},
    // 
    torecruit(index, item) {
      // 
      console.log(item);
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      this.$router.push({
        path: "/detail",
        query: { id: index, kind: "recruit" }
      });
    }
  },

  components: { banner, tabBar, moPagination, swiper, swiperSlide }
};
</script>

<style lang="less" scoped>
.about {
  width: 100%;
  .aboutbox {
    width: 100%;
    box-sizing: border-box;
    padding: 40px 0 70px 0;
    .pub {
      box-sizing: border-box;
      padding: 53px 25px 0 25px;
    }
    // 
    .cpintro {
      .topimg {
        width: 100%;
        height: 470px;
      }
      .cpinfo {
        box-sizing: border-box;
        padding: 48px 0;
        .title {
          font-size: 36px;
          color: rgba(79, 129, 189, 1);
        }
        .bold {
          box-sizing: border-box;
          padding: 10px 0;
          font-size: 24px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 30px;
        }
        .text {
          // css
          white-space: pre-line;
          color: rgba(51, 51, 51, 1);
          font-size: 16px;
          line-height: 35px;
        }
      }
      .cpshow {
        width: 100%;
        display: flex;
        justify-content: space-between;
        .cpshowli {
          width: 49%;
          .mainpic {
            width: 100%;
            height: 217px;
          }
        }
      }
    }
    // 
    .ideabox {
      .mainpic {
        width: 100%;
        height: 312px;
      }
      .culture {
        background: rgba(238, 238, 238, 1);
        border-radius: 10px;
        box-sizing: border-box;
        padding: 38px 40px;
        margin-top: 25px;
        color: #333333;
        h1 {
          font-size: 36px;
        }
        .english {
          font-size: 18px;
          font-weight: lighter;
          line-height: 48px;
        }
        .text {
          font-size: 30px;
          line-height: 50px;
          color: #666666;
        }
      }
    }
    // 
    .staffshow {
      width: 100%;
      .stafflist {
        width: 100%;
        .staffli {
          width: 100%;
          margin-bottom: 25px;
          .mainpic {
            width: 100%;
            height: 437px;
            border-radius: 10px;
          }
          .infos {
            width: 100%;
            height: 261px;
            background: rgba(245, 245, 245, 1);
            border-radius: 10px;
            color: #333333;
            box-sizing: border-box;
            padding: 30px 15px;
            .text {
              font-size: 30px;
              font-weight: bold;
            }
            .des {
              padding-top: 10px;
              font-size: 24px;
              line-height: 40px;
            }
          }
        }
      }
    }

    // 
    .Honor {
      width: 100%;
      .top {
        width: 100%;
        text-align: center;
        .title {
          color: #2482c8;
          font-size: 30px;
          font-weight: bold;
        }
        .des {
          padding-top: 10px;
          color: #666666;
          font-size: 24px;
          line-height: 35px;
        }
      }
      .honorlist {
        width: 100%;
        box-sizing: border-box;
        padding-top: 25px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .honorli {
          width: 100%;
          height: 256px;
          margin-bottom: 25px;
          .honorbox {
            display: flex;
            justify-content: space-between;
            .mainpic {
              width: 49%;
              height: 256px;
              background: RGBA(54, 105, 165, 1);
              box-sizing: border-box;
              padding: 5px 5px;
            }
            .honorinfo {
              width: 49%;
              height: 256px;
              background: rgba(236, 236, 236, 1);
              color: rgba(102, 102, 102, 1);
              text-align: center;
              .text {
                box-sizing: border-box;
                padding-top: 68px;
                .year {
                  font-family: Impact;
                  font-size: 42px;
                }
                .intro {
                  width: 50%;
                  font-size: 24px;
                  line-height: 35px;
                  margin: 0 auto;
                }
              }
            }
          }
        }
      }
      .pics {
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
    // 
    .sale {
      .topmainpic {
        width: 100%;
        height: 480px;
      }
    }
    // 
    .framework {
      width: 100%;
      .mainpic {
        width: 100%;
        height: 480px;
      }
    }
    // 
    .recruitment {
      width: 100%;
      .newsli {
        width: 100%;
        border-bottom: 1px solid rgba(204, 204, 204, 1);
        display: flex;
        justify-content: flex-start;
        .mainpic {
          margin: 30px 0;
          width: 50%;
          height: 225px;
          background: rgba(255, 255, 255, 1);
          border-radius: 10px;
        }
        .ins-info {
          width: 50%;
          box-sizing: border-box;
          padding: 30px 0 0 30px;
          .title {
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            -o-text-overflow: ellipsis;
            white-space: nowrap;
            font-size: 30px;
            color: rgba(51, 51, 51, 1);
            line-height: 45px;
          }
          .time {
            font-size: 14px;
            font-weight: 400;
            color: rgba(153, 153, 153, 1);
            line-height: 45px;
          }
          .intro {
            font-size: 24px;
            color: #5b5b5b;
            line-height: 30px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3; //
            overflow: hidden;
          }
          .more {
            width: 174px;
            height: 41px;
            border: 1px solid rgba(153, 153, 153, 1);
            border-radius: 10px;
            text-align: center;
            font-size: 20px;
            font-weight: 300;
            color: rgba(153, 153, 153, 1);
            line-height: 41px;
            margin-top: 15px;
            // &:active {
            //   border: 1px solid rgba(62, 145, 223, 1);
            //   color: rgba(62, 145, 223, 1);
            // }
          }
        }
        &:last-child {
          border: none;
          padding-bottom: 30px;
        }
      }
    }
    // 
    .contact {
      background: rgba(255, 255, 255, 1);
      .contactbox {
        width: 100%;
        box-sizing: border-box;
        padding: 35px 0px;
        line-height: 30px;
        color: rgba(51, 51, 51, 1);

        .warrper {
          width: 100%;
          height: 152px;
          position: relative;
          box-sizing: border-box;
          margin-bottom: 55px;
          h1 {
            font-size: 36px;
            text-align: center;
            color: rgba(62, 145, 223, 1);
            position: relative;
            top: 20px;
            right: 25px;
          }
          .totel {
            position: absolute;
            bottom: 25px;
            left: 5%;
            font-size: 37px;
            font-weight: bold;
            color: #ffffff;
            span {
              font-family: Impact;
              position: relative;
              top: 5px;
              font-size: 49px;
              color: white;
              font-weight: 400;
            }
          }
        }
        .text {
          border-bottom: 1px solid #ececec;
          font-size: 30px;
          line-height: 45px;
          color: #333333;
          box-sizing: border-box;
          padding: 30px 0;
          span {
            letter-spacing: 1px;
          }
        }
        .branch {
          box-sizing: border-box;
          margin-bottom: 32px;
        }
        .map {
          width: 100%;
          height: 396px;
          background: palevioletred;
        }
      }
    }
  }
}
</style>


